body{
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Monserat;
  font-size: 14px;
}
*,*:after,*:before{
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
h1{
  font-size: 24px;
  font-weight: 700;
  color: #1F2041;
}
h2{
  font-size: 19px;
  font-weight: 700;
  color: #1F2041;
}
h3{
  font-size: 12px;
  font-weight: 700;
  color: #1F2041;
}
p{
  font-size: 14px;
  font-weight: 400;
  color: rgba(31,32,65,0.75)
}
container{
  width: 100px;
  height: 540px;
}
.colorList{
  list-style-type: none;
}